import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter ({
  mode:"history",
  base:'/',
  routes:[{
    path:'/',
    props:true
  }, {
    path:'/foo/:idd',
    component:() => import('./components/foo'),
    meta:{
      fuck:'yes'
    },
    props:true,
    children:[
      {
        path:'/ka',
        component:()=>import('./components/ka'),
      }
    ]
  }, {
    path:'/bar',
    component:() => import('./components/bar'),
    alias:'/shit',
    // props:(route)=>(route.$param.)
  }]
})
export default router


