<template>
  <div>
    <button @click="handleClick">点击</button>
    <div>{{message}}</div>
    <div>{{id}}</div>
    <router-link to="/foo/1" exact>/foo1</router-link>
    <router-link to="/foo/2" exact>/foo2</router-link>
    <a href="#" @click="$router.push('/bar?sb=1')">/bar</a>
    <div class="upload-file-wrap">
      <input @change="handleUp($event)" @dragstart="dragStart($event)" @drop="handleDrop($event)" class="upload-file" type="file" draggable></input>
    </div>
    <input type="textarea" @dragstart="dragStart" @drop="handleDrop2($event)">
    <input type="textarea" @dragstart="dragStart" @drop="handleDrop2($event)">
    <canvas id="canvas" @mousedown = "handleMouseDown($event)" @mousemove = "handleMouseMove($event,img)" @mouseup="handleMouseUp"></canvas>
    <canvas id="result-canvas"></canvas>
    <img id="shit" :src="picSrc">
    <router-view></router-view>
  </div>
</template>

<script>
import Vue from "vue"
export default {
  name: 'App',
  data(){
    return {
      message: '',
      picSrc:'',
      isDown: false,
      startX: undefined,
      startY: undefined,
      img:{},
      ctx:{}
    }
  },
  props:['id'],
  methods: {
    handleDrop (event) {
      var _self = this;
      var dt = event.dataTransfer;
      dt.dropEffect = 'copy';
      var file = dt.files;
      var reader = new FileReader();
      async () => {
        if(file)
        await reader.readAsDataURL(file[0]);
         _self.picSrc = reader.result;
      }
    },
    dragStart(e){
      // event.preventDefault();
      // event.dataTransfer.effectAllowed="all";
      event.target.value = event.target.value;
    },
    handleDrop2(e){
      event.preventDefault();
      // event.dataTransfer.dropEffect="copy";
      event.target.value = event.dataTransfer.getData('text/plain');
    },
    handleClick() {
      let _self = this;
      Vue.axios.get(
        '../static/mod.js'
      ).then(function(res){
        _self.message=res;
       })
      .catch(function(err){
        _self.message=err;
      })
    },
    handleMouseDown(event) {
      this.isDown = true;
      this.startX = event.offsetX;
      this.startY = event.offsetY;
    },
    handleMouseMove(event,img) {
      let _self = this;
      if(this.isDown === true) {
        let canvas = event.target;
        let resultCanvas = document.getElementById("result-canvas");
        let resultCtx = resultCanvas.getContext("2d");
        let moveWidth = event.offsetX - this.startX;
        let moveHeight = event.offsetY - this.startY;
        moveWidth = moveWidth == 0? 1: moveWidth;
        moveHeight = moveHeight == 0? 1: moveHeight;
        this.ctx.clearRect(0,0,img.width,img.height);
        this.ctx.drawImage(img,0,0);
        this.ctx.fillStyle = "rgba(255,255,255,0.5)";
        this.ctx.fillRect(0,0,img.width,img.height);
        this.ctx.drawImage(img,this.startX,this.startY,moveWidth,moveHeight,this.startX,this.startY,moveWidth,moveHeight);

        let cutdata = this.ctx.getImageData(this.startX,this.startY,moveWidth,moveHeight);
        resultCanvas.width = moveWidth;
        resultCanvas.height = moveHeight;
        resultCtx.clearRect(0,0,resultCanvas.width,resultCanvas.height);
        resultCtx.putImageData(cutdata,0,0);

        resultCanvas.toBlob(function(blob) {
           _self.picSrc = URL.createObjectURL(blob);
           document.getElementById("shit").onload = function(){
            URL.revokeObjectURL(_self.picSrc);
        }
        });
      }
    },
    handleMouseUp(){
      this.isDown = false;
    },
    readFile(file) {
      return new Promise((resolve, reject) => {
        try {
          let reader = new FileReader();
          reader.readAsDataURL(file);
          reader.addEventListener("load", ()=>{
          resolve(reader.result);
          })
        } catch(e) {
          reject(e);
        }
      });
    },

    drawCanvas(img){
      var canvas = document.getElementById("canvas");
      this.ctx = canvas.getContext('2d');
      try {
        canvas.width = img.width;
        canvas.height = img.height;
        this.ctx.drawImage(img,0,0);
      } catch(e){
        throw new Error(e);
      }
    },
    handleUp(event) {
      let _self = this;
      let data = new FormData();
      let pic = event.target.files[0];
      data.append("image",pic);
      
      Vue.axios({
        method: 'post',
        url: './serve.js',
        data: data
      }).then (
        res => console.log(res)
      );      

      (async function() {
        _self.img = new Image();
        _self.img.src = await _self.readFile(pic);
        _self.drawCanvas(await _self.img);
      })();
    }
  },
  watch:{
    $route:function(newval,old){
      console.log(this);
    }
  }
}
</script>

<style>
  :root {
    --mainHeight: calc(100px - 1px);
    --color: white;
  }

  .upload-file-wrap {
    width: var(--mainHeight);
    display: inline-block;
    background: var(--color);
  }

  .upload-file {
    width: 200px;
    opacity: 0;
  }

  .upload-file-wrap:hover {
    opacity: 0.8;
  }

</style>



