import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state:{
    count:1,
    count3:0,
    count4:0,
    type:undefined,
    shit:undefined
  },
  mutations:{
    add(state,val){
      state.count+=val;
    },
    jian(state,payload){
      state.count4+=payload.count;
    },
    eatShit(state,val) {
      state.shit=val;
    }
  },
  actions:{
    typ({commit},val){
      new Promise((res,rej)=>{
        setTimeout(()=>res(),1000); 
      }).then(()=>commit('add',val))
    }
  }
});
export default store;



